.card-container {
  max-width: 420px;
  height: 180px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
  user-select: none;
  transition: transform 150ms ease-in-out;
  /* display: grid; */
  /* grid-template-rows: 30% 1fr; */
  /* align-items: center; */
}

.card-container:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.card-container * {
  outline: none;
}

.card__header {
  font-weight: bold;
  padding: 30px;
  font-size: 20px;
}

.card__body {
  display: grid;
  justify-content: center;
  align-content: center;
}
